"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Slider } from '@/components/ui/slider'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { ScoringWeights } from '@/lib/leadScoring'

const DEFAULT_WEIGHTS: ScoringWeights = {
  engagement: 0.3,
  companySize: 0.2,
  industry: 0.15,
  source: 0.15,
  behavior: 0.2,
}

export default function SettingsPage() {
  const [weights, setWeights] = useState<ScoringWeights>(DEFAULT_WEIGHTS)
  const [isSaved, setIsSaved] = useState(false)

  const handleWeightChange = (key: keyof ScoringWeights, value: number[]) => {
    setWeights(prev => ({
      ...prev,
      [key]: value[0] / 100
    }))
    setIsSaved(false)
  }

  const handleSave = () => {
    // In a real application, this would save to a backend
    // For now, we'll just show a success message
    setIsSaved(true)
    setTimeout(() => setIsSaved(false), 3000)
  }

  const handleReset = () => {
    setWeights(DEFAULT_WEIGHTS)
    setIsSaved(false)
  }

  const totalWeight = Object.values(weights).reduce((sum, weight) => sum + weight, 0)
  const isValidWeights = Math.abs(totalWeight - 1) < 0.01 // Allow small floating point differences

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      <div>
        <h1 className="text-3xl font-bold">Settings</h1>
        <p className="text-muted-foreground">
          Configure your lead scoring algorithm and system preferences
        </p>
      </div>

      {isSaved && (
        <Alert>
          <AlertDescription>
            Settings have been saved successfully!
          </AlertDescription>
        </Alert>
      )}

      {!isValidWeights && (
        <Alert variant="destructive">
          <AlertDescription>
            Warning: Scoring weights must total 100%. Current total: {Math.round(totalWeight * 100)}%
          </AlertDescription>
        </Alert>
      )}

      {/* Scoring Weights Configuration */}
      <Card>
        <CardHeader>
          <CardTitle>Lead Scoring Weights</CardTitle>
          <CardDescription>
            Adjust the importance of different factors in your lead scoring algorithm. 
            All weights must total 100%.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="space-y-4">
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <Label htmlFor="engagement">Engagement Score</Label>
                <span className="text-sm text-muted-foreground">
                  {Math.round(weights.engagement * 100)}%
                </span>
              </div>
              <Slider
                id="engagement"
                min={0}
                max={100}
                step={1}
                value={[weights.engagement * 100]}
                onValueChange={(value) => handleWeightChange('engagement', value)}
                className="w-full"
              />
              <p className="text-xs text-muted-foreground">
                How much the lead's overall engagement level affects their score
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <Label htmlFor="companySize">Company Size</Label>
                <span className="text-sm text-muted-foreground">
                  {Math.round(weights.companySize * 100)}%
                </span>
              </div>
              <Slider
                id="companySize"
                min={0}
                max={100}
                step={1}
                value={[weights.companySize * 100]}
                onValueChange={(value) => handleWeightChange('companySize', value)}
                className="w-full"
              />
              <p className="text-xs text-muted-foreground">
                Impact of the lead's company size on their score
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <Label htmlFor="industry">Industry</Label>
                <span className="text-sm text-muted-foreground">
                  {Math.round(weights.industry * 100)}%
                </span>
              </div>
              <Slider
                id="industry"
                min={0}
                max={100}
                step={1}
                value={[weights.industry * 100]}
                onValueChange={(value) => handleWeightChange('industry', value)}
                className="w-full"
              />
              <p className="text-xs text-muted-foreground">
                How much the lead's industry affects their score
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <Label htmlFor="source">Lead Source</Label>
                <span className="text-sm text-muted-foreground">
                  {Math.round(weights.source * 100)}%
                </span>
              </div>
              <Slider
                id="source"
                min={0}
                max={100}
                step={1}
                value={[weights.source * 100]}
                onValueChange={(value) => handleWeightChange('source', value)}
                className="w-full"
              />
              <p className="text-xs text-muted-foreground">
                Impact of where the lead came from on their score
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <Label htmlFor="behavior">Behavioral Data</Label>
                <span className="text-sm text-muted-foreground">
                  {Math.round(weights.behavior * 100)}%
                </span>
              </div>
              <Slider
                id="behavior"
                min={0}
                max={100}
                step={1}
                value={[weights.behavior * 100]}
                onValueChange={(value) => handleWeightChange('behavior', value)}
                className="w-full"
              />
              <p className="text-xs text-muted-foreground">
                How much behavioral activities (visits, clicks, etc.) affect the score
              </p>
            </div>
          </div>

          <div className="flex gap-4 pt-4 border-t">
            <Button 
              onClick={handleSave} 
              disabled={!isValidWeights}
            >
              Save Changes
            </Button>
            <Button 
              variant="outline" 
              onClick={handleReset}
            >
              Reset to Defaults
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Scoring Categories */}
      <Card>
        <CardHeader>
          <CardTitle>Score Categories</CardTitle>
          <CardDescription>
            Current lead score categories and their thresholds
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 bg-red-600 rounded-full"></div>
                <span className="font-medium">Hot Leads</span>
              </div>
              <p className="text-sm text-muted-foreground">Score: 80-100</p>
              <p className="text-xs text-muted-foreground">
                High-priority leads ready for immediate contact
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 bg-orange-600 rounded-full"></div>
                <span className="font-medium">Warm Leads</span>
              </div>
              <p className="text-sm text-muted-foreground">Score: 60-79</p>
              <p className="text-xs text-muted-foreground">
                Qualified leads with good conversion potential
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 bg-blue-600 rounded-full"></div>
                <span className="font-medium">Cold Leads</span>
              </div>
              <p className="text-sm text-muted-foreground">Score: 40-59</p>
              <p className="text-xs text-muted-foreground">
                Potential leads that need nurturing
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 bg-gray-600 rounded-full"></div>
                <span className="font-medium">Low Priority</span>
              </div>
              <p className="text-sm text-muted-foreground">Score: 0-39</p>
              <p className="text-xs text-muted-foreground">
                Low-priority leads with minimal engagement
              </p>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* System Information */}
      <Card>
        <CardHeader>
          <CardTitle>System Information</CardTitle>
          <CardDescription>
            Information about your lead scoring system
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <h4 className="font-medium">Scoring Factors</h4>
              <ul className="text-sm text-muted-foreground space-y-1">
                <li>• Engagement level (0-100)</li>
                <li>• Company size (startup to enterprise)</li>
                <li>• Industry type</li>
                <li>• Lead source</li>
                <li>• Website visits</li>
                <li>• Email opens and clicks</li>
                <li>• Form submissions</li>
              </ul>
            </div>

            <div className="space-y-4">
              <h4 className="font-medium">Data Storage</h4>
              <p className="text-sm text-muted-foreground">
                Currently using in-memory storage for development. 
                In production, this would be connected to a database 
                for persistent data storage.
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
